.Notecard {
  width:100%;
  margin:auto;
  max-width: 600px;
  min-width:256px;
  border-color: gray;
  border-style: solid;
  border-width: 1px;
  height:320px;
  border-collapse: separate;
  font-family: 'Karma', serif;
  font-size:1.5em;
}
.cardTextContainer {
  position: relative; margin: 0 auto; text-align: center
}
.cardTextSubcontainer {
  position: absolute; top: 0; width: 100%
}
.cardFrontText {
}
.cardBackText {
  -webkit-transform:rotateY(-180deg);
          transform:rotateY(-180deg);
  visibility: hidden;
}

.swap-enter {
  visibility: visible;
  -webkit-transition-delay: 0.25s;
       -o-transition-delay: 0.25s;
          transition-delay: 0.25s;
}
.swap-enter.swap-enter-active {
  -webkit-transition: .25s;
  -o-transition: .25s;
  transition: .25s;
}
.swap-exit {
  visibility: hidden;
  -webkit-transition-delay: .25s;
       -o-transition-delay: .25s;
          transition-delay: .25s;
}
.swap-exit.swap-exit-active {
  -webkit-transition:  .25s;
  -o-transition:  .25s;
  transition:  .25s;
}

.flip-enter {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}
.flip-enter.flip-enter-active {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  -webkit-transition: -webkit-transform .5s ease-in-out;
  transition: -webkit-transform .5s ease-in-out;
  -o-transition: transform .5s ease-in-out;
  transition: transform .5s ease-in-out;
  transition: transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
}
.flip-exit {
  opacity:0;
  -webkit-transform:translateX(32px);
      -ms-transform:translateX(32px);
          transform:translateX(32px);
}
.flip-exit.flip-exit-active {
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
  -webkit-transition:
    opacity .25s .5s,
    -webkit-transform .5s ease-in-out;
  transition:
    opacity .25s .5s,
    -webkit-transform .5s ease-in-out;
  -o-transition:
    transform .5s ease-in-out,
    opacity .25s .5s;
  transition:
    transform .5s ease-in-out,
    opacity .25s .5s;
  transition:
    transform .5s ease-in-out,
    opacity .25s .5s,
    -webkit-transform .5s ease-in-out;

}



